<template>
  <div class="Header" @click="emitChangerTitle">
    <h1>{{ title }}</h1>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
    },
  },
  data() {
    return {
      // title: "Vue Components Demo"
    };
  },
  methods: {
    emitChangerTitle() {
      this.$emit("changeTitle", "修改后的标题");
    },
  },
};
</script>

<style scoped>
.Header {
  background: lightgreen;
  padding: 10px;
}
h1 {
  color: #222;
  text-align: center;
}
</style>